<template>
  <v-app id="auth-tema">
    <v-container
      class="primary"
      fluid
      fill-height
    >
      <div
        v-show="alert"
        class="alert-info"
      >
        <v-alert 
          type="success"
        >
          <span v-html="alert"></span>
        </v-alert>

        <btn-secondary
          v-if="alertGoBack"
          class="mt-8 btn-goback d-flex"
          text="Voltar"
          :block="false"
          @clicked="voltar"
        />
      </div>

      <div 
        v-show="!alert"
        class="box-height"
      >
        <v-card
          class="card auth-tema-card-main primaryDark"
          elevation="6"
          dark
        >
          <div class="overline mb-1">
            Meu Barbeiro
          </div>

          <v-card-title class="v-card-title mb-4">
            {{ titulo }}
          </v-card-title>

          <slot></slot>       
        </v-card>
        <app-progress v-show="progressLoader" />
      </div>
    </v-container>
  </v-app>
</template>

<script>
import AppProgress from '@/components/globais/progress/Progress'
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import NavigationMixin from '@/mixins/NavigationMixin'

export default {
  name: 'Template',
  components: { AppProgress, BtnSecondary },
  mixins: [NavigationMixin],
  props: {
    titulo: { required: true },
    progressLoader: { default: false },
    alert: { default: null },
    alertGoBack: { default: true },
    isBarber: { default: false }
  },
  methods: {
    voltar () {
      if (this.isBarber) this.goTo('login-barber')
      else this.goTo('login')
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';

#auth-tema {
  .alert-info, .btn-goback { margin: auto; }
  .box-height {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    left: 0;
    right: 0;
    display: block;
    width: fit-content;
    height: fit-content;
    max-width: calc(100% - 30px);
    .card { border-top: 4px solid $secondary !important; padding: 30px; width: 320px;
      .v-card-title { padding: 0; }    
    }
  }
}
</style>